/* Logo y menú */

#navbarDropdown {
    min-width: 100px;
    text-align: left;
}
nav {
    margin-top: 1em !important;
    font-size: 14px!important;

}

.navbar {
    padding-top: 0;
}

.navbar-dark .navbar-nav .nav-link {
    color: white !important;
}

header {
    position: relative;
    border: 1px solid transparent;
    margin: -1.3em auto 2em auto;
    background-color: #00a3a3;
    padding: 0.2em;
}

.navbar-toggler {
    order: -1;
}

nav ul li {
    text-transform: uppercase;
    text-align: center;
    margin: .3em 0;
    background-color: none;

}

nav ul {
    margin-top: 2em;
}

nav ul li a {
    color: white;
    font-weight: 600;
    font-size: 14px!important;

}

nav ul li a:hover {
    color: #00a3a3;
}

.navlink  {
    color: white;
    font-size: 14px!important;
}

#navbarDropdown {
    text-align: center;
}
.dropdown-item {
	color: #00a3a3 !important;
	font-weight: bold !important;

}


@media screen and (min-width: 990px) {

    nav ul li {
        font-weight: bold;
        padding: 0 1em;
    }
    nav ul {
        margin: 0 5em 0 0;
    }

    #navbarDropdown {
        min-width: 100px;
        text-align: left;
    }
}

#logo {
    background: url(data:image/png;base64,) no-repeat center center;
    width: 80vw;
    height: calc((80vw - 0.625em) * 3.1428);
    background-size: cover;
    font-size: 0;
    max-width: 110px;
    max-height: 35px;
    display: inline-block;
    vertical-align: middle;
    position: absolute;
    top: 20px;
    left: calc(50% - 35px)
}

@media screen and (min-width: 990px) {
    #logo {
        background: url(data:image/png;base64,) no-repeat center center;
        width: 80vw;
        height: calc((80vw - 0.625em) * 3.1071);
        max-width: 174px;
        max-height: 56px;
        min-width: 174px;
        min-height: 56px;
        margin-right: 0;
        top: 10px;
        left: 10px;
    }
}

@media screen and (min-width: 481px) and (max-width: 990px) {
    #logo {
        background: url(data:image/png;base64,) no-repeat center center;
        width: 80vw;
        height: calc((80vw - 0.625em) * 3.125);
        max-width: 125px;
        max-height: 40px;
        top: 20px;
        left: calc(50% - 40px);
    }
}


